import React, { useContext, useEffect, useState } from "react";
import styles from '../../MapBox.module.scss';
import { TMeshObjectContext, useMeshObjectContext } from "src/contexts/MeshObjectContextProvider";
import { IConfig } from "src/types";

export const MapCenterMarker = ({ size, lockSize, blink, cameraLength, config }: { size: number, lockSize?: boolean, blink: boolean, cameraLength: number | undefined, config: IConfig | undefined }) => {
  const [sizeFactor, setSizeFactor] = useState<number>(1) // 1 - keep original size value
  const kioskElementRef = React.useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!lockSize && config !== undefined && cameraLength !== undefined) {
      setSizeFactor(1 - (cameraLength - config.CAMERA.minDistance) / (config.CAMERA.maxDistance - config.CAMERA.minDistance))
    }
  }, [lockSize, config, cameraLength])

  useEffect(() => {
    if (lockSize) {
      setSizeFactor(1);
    }
  }, [lockSize]);

  // todo figure out better way
  useEffect(() => {
    if (!kioskElementRef.current) return;
    const kioskElement = kioskElementRef.current;
    const svgOrigin = document.querySelector('#map-special-svg-kiosk svg');
    let svgClone: Node;
    if (svgOrigin) {
      svgClone = svgOrigin.cloneNode(true);
      kioskElement.appendChild(svgClone);
    }

    return () => {
      if (svgClone) {
        kioskElement.removeChild(svgClone);
      }
    }
  }, []);

  return <>
    <div className={styles['kioskMarker']} ref={kioskElementRef} style={{ width: sizeFactor * size * 2, height: sizeFactor * size * 10 }}></div>
    {blink ? <div className={styles['mapCenterMarker']}></div> : null}
  </>
}